.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
  border-bottom: 0.01rem solid var(--text-color2);
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__title p {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text-color2);
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text-color2);
}