@use '../util/' as *;

.header{
    display: flex;
    flex-wrap: wrap;
    margin-top: rem(36);

    @include breakpoint(medium) {
        justify-content: space-between;
        align-items: center;
    }

    &__title{
        width: 100%;
        border-bottom: 0.01rem solid  var(--text-color2);
        
        @include breakpoint(medium) {
            width: auto;
        }
        p{
            font-size: rem(14);
            font-weight: 700;
            color: var(--text-color2);
        }
    }

    &__subtitle {
        font-size: rem(14);
        font-weight: 700;
        color: var(--text-color2);
    }
}
